<template>
    <div
        :style="[{'position':'relative','overflow-y':'auto','overflow-x':'hidden','height':`${listHeight}px`}]"
        @scroll="scrollItems"
    >

        <div :style="[{'height':`${contentHeight}px`}]"></div>

        <template v-for="(item,kitem) in useItems">
            <template v-if="item.screenY<=contentHeight">
                <div
                    ref="itemDiv"
                    :nowShow="item.nowShow"
                    :style="[{'position':'absolute','top':`${item.screenY}px`,'width':'100%','opacity':item.nowShow?1:0.01}]"
                    :index="item.index"
                    :key="kitem"
                >
                    <slot
                        name="block"
                        :row="item.row"
                        :irow="item.index"
                    ></slot>
                </div>
            </template>
        </template>

    </div>
</template>

<script>
import each from 'lodash/each'
import map from 'lodash/map'
import get from 'lodash/get'
import size from 'lodash/size'
import debounce from 'lodash/debounce'
import cint from 'wsemi/src/cint.mjs'
import genID from 'wsemi/src/genID.mjs'
import genPm from 'wsemi/src/genPm.mjs'
import isarr from 'wsemi/src/isarr.mjs'
import binarySearch from '../js/binarySearch.mjs'
import globalMemory from '../js/globalMemory.mjs'

//gm
let gm = globalMemory()

/**
 * @vue-prop {Array} [rows=[]] []slotslotrowirowrowsslotmargin
 * @vue-prop {Number} [listHeight=400] px400
 * @vue-prop {Number} [itemMinHeight=24] px24
 * @vue-prop {Number} [itemsPreload=40] 40
 */
export default {
    props: {
        rows: {
            type: Array,
            default: () => [],
        },
        listHeight: {
            type: Number,
            default: 400,
        },
        itemMinHeight: {
            type: Number,
            default: 24,
        },
        itemsPreload: {
            type: Number,
            default: 40,
        },
    },
    data: function() {
        return {
            //itemDivstylewidth:100%ie11flex
            mmkey: null,
            changeHeight: true, //, true
            scrollTop: 0, //
            scrollRatio: 1, //
            contentHeight: 0, //, scrollHeight, 16px, contentHeight
            contentHeightMax: 1e7, //, chrome1.4e7px, , 
            itemsHeight: 0, //
            //items: [], //
            useItems: [], //
            itemHasShow: false, //show, 
            modifyFromOuter: false, //, watchchange
        }
    },
    beforeDestroy: function() {
        //console.log('beforeMount')

        let vo = this

        //remove
        gm.remove(vo.mmkey)

    },
    mounted: function() {
        //console.log('mounted')

        let vo = this

        //changeView, watchchange, mounted
        vo.changeView()

    },
    watch: {

        rows: {
            immediate: true,
            deep: true,
            handler(value) {
                //console.log('watch rows', value)

                let vo = this

                //check
                if (size(value) === 0) {
                    return
                }

                //check, , 
                if (!vo.modifyFromOuter) {

                    //changeRows
                    vo.changeRows(value)

                }

            }
        }

    },
    computed: {
    },
    methods: {

        checkRows: function(rows) {
            //console.log('methods checkRows', rows)

            //check
            if (!isarr(rows)) {
                let msg = 'rows is not array'
                //console.log(msg)
                return msg
            }
            if (size(rows) === 0) {
                let msg = 'rows is empty'
                //console.log(msg)
                return msg
            }

            return ''
        },

        changeRows: function(rows) {
            //console.log('methods changeRows', rows)

            let vo = this

            //check
            if (vo.checkRows(rows) !== '') {
                return
            }

            //mmkey
            if (vo.mmkey === null) {

                //mmkey
                vo.mmkey = genID()

            }
            else {

                //remove
                gm.remove(vo.mmkey)

            }

            //itemHasShow
            vo.itemHasShow = get(rows, '[0].show', false)

            //items, lodashnew Arrayfor+push
            let items = map(rows, (v, k) => {
                return {
                    index: k,
                    height: vo.itemMinHeight,
                    y: k * vo.itemMinHeight,
                    screenY: 0, //y
                    nowShow: false, //
                    row: v,
                }
            })

            //save
            //vo.items = items
            gm.set(vo.mmkey, items)

            //changeView
            vo.changeView()

        },

        changeView: async function(forceUpdate = false) {
            //console.log('methods changeView', forceUpdate)

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //core
            let i = 0
            function core() {
                let pm = genPm()

                //count
                i += 1

                //forceUpdate, 1, 2, updateItems
                if (i >= 2) {
                    forceUpdate = false
                }

                //genUseItems
                vo.genUseItems()

                //setTimeout
                setTimeout(function() {
                    let b = vo.updateItems(forceUpdate)
                    pm.resolve(b)
                }, 10)

                //break, 5, , , 
                if (i >= 5) {
                    pm.resolve(false)
                }

                return pm
            }

            //, , , , , 
            let r = await core()
            while (r) {
                r = await core()
            }

        },

        genUseItems: function() {
            //console.log('methods genUseItems')

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //items
            //let items = vo.items
            let items = gm.get(vo.mmkey)

            //y1, y2
            let y1
            let y2
            let he = vo.contentHeight - vo.listHeight
            let heAll = vo.itemsHeight
            let r1
            let r2
            let scrollTop
            if (he >= 0) {
                scrollTop = Math.min(vo.scrollTop, he)
                let r = scrollTop / he //0~1
                let rd12 = vo.listHeight / vo.scrollRatio / he //, 
                r1 = r / (1 + rd12) //
                r1 = Math.max(r1, 0)
                r2 = r1 + rd12 //
                r2 = Math.min(r2, 1)
                y1 = r1 * heAll
                y2 = r2 * heAll
            }
            else {
                scrollTop = 0
                r1 = 0
                r2 = 1
                y1 = 0
                y2 = vo.itemsHeight
            }

            //n
            let n = size(items)

            //indStart, 
            let indStartActual = binarySearch(items, (ind) => {
                let v = items[ind]
                let dy = y1 - (v.y + v.height)
                return dy
            })
            if (indStartActual === null) {
                indStartActual = 0
            }
            let indStart = Math.max(indStartActual - vo.itemsPreload, 0)

            //indEnd, 
            let indEndActual = binarySearch(items, (ind) => {
                let v = items[ind]
                let dy = y2 - v.y
                return dy
            })
            if (indEndActual === null) {
                indEndActual = n - 1
            }
            let indEnd = Math.min(indEndActual + vo.itemsPreload, n - 1)

            //useItems
            let useItems = []
            for (let k = indStart; k <= indEnd; k++) {
                let v = items[k]
                v.screenY = ((v.y / heAll) - r1) * vo.contentHeight * vo.scrollRatio + scrollTop //ypx
                v.nowShow = k >= indStartActual //
                if (vo.itemHasShow) {
                    if (v.row.show) {
                        useItems.push(v)
                    }
                }
                else {
                    useItems.push(v)
                }
            }

            //save
            vo.useItems = useItems

        },

        updateItems: function(forceUpdate = false) {
            //console.log('methods updateItems', forceUpdate)

            let vo = this

            //check
            if (vo.mmkey === null) {
                return false
            }

            //items
            //let items = vo.items
            let items = gm.get(vo.mmkey)

            //n
            let n = size(items)

            //update height
            each(vo.$refs.itemDiv, (v) => {
                let nowShow = v.getAttribute('nowShow')
                if (nowShow) {
                    let index = cint(v.getAttribute('index'))
                    if (index >= 0 && index < n) {
                        let h = v.clientHeight //margin
                        if (items[index].height !== h) {
                            items[index].height = h
                            vo.changeHeight = true
                        }
                    }
                }
            })

            //check
            let b = vo.changeHeight || forceUpdate
            if (b) {

                //update y
                let y = 0
                if (vo.itemHasShow) {
                    for (let i = 0; i < n; i++) {
                        let v = items[i]
                        if (v.y !== y) {
                            v.y = y
                        }
                        if (v.row.show) {
                            y += v.height
                        }
                    }
                }
                else {
                    for (let i = 0; i < n; i++) {
                        let v = items[i]
                        if (v.y !== y) {
                            v.y = y
                        }
                        y += v.height
                    }
                }

                //update itemsHeight, contentHeight, scrollRatio
                if (vo.itemsHeight !== y) {
                    vo.itemsHeight = y
                    vo.contentHeight = Math.min(vo.itemsHeight, vo.contentHeightMax)
                    vo.scrollRatio = vo.itemsHeight / vo.contentHeight
                }

                //reset
                vo.changeHeight = false

            }

            return b
        },

        modifyItemsFromOuter: function(cb) {
            //console.log('methods modifyItemsFromOuter')

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //lock
            vo.modifyFromOuter = true

            //items
            //let items = vo.items
            let items = gm.get(vo.mmkey)

            //callback
            cb(items)

            //setTimeout, watch, unlockcbwatchchange
            //change 1, 
            setTimeout(function() {

                //unlock
                vo.modifyFromOuter = false

                //changeView
                vo.changeView(true)

            }, 10)

        },

        scrollItems: debounce(function(e) {
            //console.log('methods scrollItems', e.target.scrollTop)

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //setTimeout
            setTimeout(() => {

                //save
                vo.scrollTop = e.target.scrollTop

                //changeView
                vo.changeView()

            }, 10)

        }, 100),

    },
}
</script>

